.banner {
	display: flex;
	justify-content: center;
	align-items: center;
}
.banner_img {
	position: relative;
	width: 1400px;
	height: 223px;
	border-radius: 25px;
}
.banner_dark {
	background-color: $background-color_1;
	position: relative;
	width: inherit;
	height: inherit;
	border-radius: 25px;
	object-fit: cover;
}
.banner_txt {
	position: absolute;
	color: $color_1;
	text-shadow: 1px 1px 10px black;
	font-size: 48px;
	font-weight: 700;
}
@media screen and (min-width: 400px) and (max-width: 700px) {
	.banner_img {
		width: 90%;
		height: 100px;
		border-radius: 10px;
	}
	.banner_txt {
		position: absolute;
		color: $color_1;
		text-shadow: 1px 1px 10px black;
		font-size: 24px;
		font-weight: 700;
		width: 240px;
		left: 10%;
	}
}
@media screen and (min-width: 701px) and (max-width: 1440px) {
	.banner_img {
		width: 90%;
		height: 90%;
	}
	.banner_txt {
		position: absolute;
		color: $color_1;
		text-shadow: 1px 1px 10px black;
		font-size: 36px;
		font-weight: 700;
		width: 360px;
		left: 10%;
	}
}
