.description_info {
	margin-left: 20px;
	margin-right: 20px;
}
.description_info_titletags_title {
	display: flex;
	flex-direction: column;
}
.title_logement {
	color: $color_2;
	font-size: 36px;
	font-weight: 500;
	margin-top: 20px;
	width: 90%;
}
.location_logement {
	font-size: 18px;
	margin-top: 2px;
}
.description_info_titletags_tags {
	display: flex;
	flex-direction: row;
	margin-top: 30px;
}
.description_info_owner_name, .description_info_owner_rate {
	display: flex;
	justify-content: right;
	position: relative;
	bottom: 110px;
}
.description_content {
	position: relative;
	bottom: 80px;
	display: flex;
	justify-content: space-between;
	margin-left: 15px;
	margin-right: 15px;
}
.description_content_description, .description_content_equipment {
	width: 48%;
}

@media (max-width: 1024px) {
	.description_content {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-top: 130px;
	}
	.description_content_description, .description_content_equipment {
		width: 95%;
	}
	.description_info_owner {
		display: flex;
		flex-direction: row-reverse;
		justify-content: space-between;
	}
	.description_info_owner_name, .description_info_owner_rate {
		top: 20px;
	}
}
